export default {
	data() {
		return {
			userData:{
				collection:13,
				follow:1,
				viewed:3,
				points:0,
				coupon:3,
				orderNum:0,
				payedOrderNum:0,
				balance:0
			},
			likeList:[]
		};
	},
	
	onLoad() {
		this.loadData()
	},
	
	methods:{
		loadData(){
			this.getLikeList()
		},
		
		navigateToItemDetail(id){
			uni.navigateTo({
				url:`../goodsDetail/goodsDetail?id=${id}`
			})
		},
		
		async getLikeList() {
			const likeList = await this.$api.getData('likeList')
			this.likeList = likeList
		},
	}
}
